import { defineStore } from 'pinia'
import { ref } from 'vue'
import { userApi } from '../api/api'

export interface LoginRequest {
  username: string;
  password: string;
}

export const useUserStore = defineStore('user', () => {
  const token = ref<string>(localStorage.getItem('token') || '')
  const username = ref<string>(localStorage.getItem('username') || '')
  const userId = ref<string>(localStorage.getItem('userId') || '')
  const userGroupId = ref<string>(localStorage.getItem('userGroupId') || '')

  const login = async (inputUsername: string, password: string) => {
    try {
      console.log('Login attempt:', { inputUsername, password })
      
      // 使用 API 进行登录验证
      const loginResult = await userApi.login({ 
        username: inputUsername, 
        password: password 
      })
      
      if (loginResult.token && loginResult.userId) { 
        // 登录成功，保存用户信息和 token
        token.value = loginResult.token
        userId.value = loginResult.userId
        userGroupId.value = loginResult.userGroupId
        
        // 保存到本地存储
        localStorage.setItem('token', loginResult.token)
        localStorage.setItem('userId', loginResult.userId)
        localStorage.setItem('userGroupId', loginResult.userGroupId) 
        return true
      }
      
      console.log('Login failed: invalid credentials')
      return false
    } catch (error) {
      console.error('Login failed:', error)
      return false
    }
  }

  const logout = () => { 
    // 清除 token 和用户信息
    token.value = '' 
    userId.value = ''
    userGroupId.value = ''
     
    // 清除本地存储
    localStorage.removeItem('token') 
    localStorage.removeItem('userId')
    localStorage.removeItem('userGroupId')
  }

  return { 
    token, 
    userId,
    userGroupId,
    login,
    logout
  }
}) 