<template>
  <div class="system-container">
    
    <!-- 系统信息 -->
    <el-card class="box-card mb-4">
      <template #header>
        <div class="card-header">
          <span>系统信息</span>
          <el-button type="primary" link @click="fetchSystemInfo">
            <el-icon><Refresh /></el-icon>
          </el-button>
        </div>
      </template>
      <el-descriptions :column="2" border v-loading="loading.info">
        <el-descriptions-item label="启动时间">{{ systemInfo.startUpTime }}</el-descriptions-item>
        <el-descriptions-item label="版本">{{ systemInfo.version }}</el-descriptions-item>
      </el-descriptions>
    </el-card>
    
    <!-- 基本设置 -->
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span>基本设置</span>
        </div>
      </template>
      <el-form :model="form" label-width="120px">
        <el-form-item label="系统名称">
          <el-input v-model="form.systemName" />
        </el-form-item>
        <el-form-item label="系统公告">
          <el-input 
            v-model="form.description" 
            type="textarea" 
            :rows="7" 
          />
        </el-form-item>
        <el-form-item label="流量记录保留">
          <el-input-number 
            v-model="form.trafficRecordDays" 
            :min="1" 
            :max="90" 
          />
          <span class="form-help">天</span>
        </el-form-item>
        <el-form-item>
          <el-button 
            type="primary" 
            @click="handleSave" 
            :loading="loading.save"
          >
            保存设置
          </el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted, onUnmounted } from 'vue'
import { ElMessage } from 'element-plus'
import { systemApi } from '../api/api'
import type { SystemInfo, UpdateSystemInfoRequest } from '../api/apiTypes'
import { Refresh } from '@element-plus/icons-vue'

const systemInfo = ref<SystemInfo>({
  systemName: '',
  description: '',
  version: '',
  startUpTime: '',
  trafficRecordDays: 30
})

const form = reactive<UpdateSystemInfoRequest>({
  systemName: '',
  description: '',
  trafficRecordDays: 30
})

const loading = reactive({
  save: false,
  info: false
})

// 获取系统信息
const fetchSystemInfo = async () => {
  loading.info = true
  try {
    const info = await systemApi.getSystemInfo()
    systemInfo.value = info
    // 同步可编辑字段到表单
    form.systemName = info.systemName
    form.description = info.description
    form.trafficRecordDays = info.trafficRecordDays
  } catch (error) {
    console.error('获取系统信息失败', error)
    ElMessage.error('获取系统信息失败')
  } finally {
    loading.info = false
  }
}

// 保存设置
const handleSave = async () => {
  loading.save = true
  try {
    await systemApi.updateSystemInfo(form)
    ElMessage.success('设置保存成功')
    // 刷新系统信息
    await fetchSystemInfo()
  } catch (error) {
    console.error('保存设置失败', error)
    ElMessage.error('保存设置失败')
  } finally {
    loading.save = false
  }
}

onMounted(() => {
  fetchSystemInfo()
})

// 定时刷新系统信息
const infoInterval = setInterval(fetchSystemInfo, 60000) // 每分钟更新一次
onUnmounted(() => {
  clearInterval(infoInterval)
})
</script>

