            </div>  
            <router-link to="/" class="dock-item" :class="{ active: route.path === '/' }">
              <el-icon><DataAnalysis /></el-icon>
              <span>统计数据</span>
            </router-link>  
            <router-link to="/integrated" class="dock-item" :class="{ active: route.path === '/integrated' }">
              <el-icon><Operation /></el-icon>
              <span>管理面板</span>
            </router-link> 
            <router-link to="/system" class="dock-item" :class="{ active: route.path === '/system' }">
              <el-icon><Setting /></el-icon>
              <span>基础设置</span>
            </router-link>
          </template>
          
          <!-- 普通用户菜单 -->
          <template v-else>
            <router-link to="/my-proxies" class="dock-item" :class="{ active: route.path === '/my-proxies' }">
