      <!-- 用户二级菜单 -->
      <div class="user-submenu" v-show="showUserMenu">
        <div class="submenu-item" @click="handleCommand('changePassword')">
          <el-icon><Setting /></el-icon>
          <span>修改密码</span>
        </div>
        <div class="submenu-item" @click="handleCommand('logout')">
          <el-icon><SwitchButton /></el-icon>
          <span>退出登录</span>
        </div>
      </div>
    </div>

    <!-- 移动端底部导航栏 -->
