    <template #footer>
      <span class="dialog-footer">
        <el-button @click="changePasswordDialogVisible = false">取消</el-button>
        <el-button type="primary" @click="submitChangePassword" :loading="changePasswordLoading">确定</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script setup lang="ts">
import { ref, onMounted, reactive,  onUnmounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { Monitor, Connection, User, Share, ArrowDown, Setting, Management, SwitchButton, DataAnalysis } from '@element-plus/icons-vue'
import { useUserStore } from '../stores/user'
import {  userApi, systemApi } from '../api/api'
import { ElMessage } from 'element-plus'
import type { FormInstance, FormRules } from 'element-plus'

const route = useRoute()
const router = useRouter()
const userStore = useUserStore()
const isAdmin = ref(false)
const systemName = ref('ZIP管理面板')

// 移动端检测
const isMobile = ref(false) 
// 修改密码相关
const changePasswordDialogVisible = ref(false)
const changePasswordLoading = ref(false)
const passwordFormRef = ref<FormInstance>()
const passwordForm = reactive({
  oldPassword: '',
  newPassword: '',
  confirmPassword: ''
})

const passwordRules = reactive<FormRules>({
  oldPassword: [
    { required: true, message: '请输入旧密码', trigger: 'blur' }
  ],
  newPassword: [
    { required: true, message: '请输入新密码', trigger: 'blur' },
    { min: 6, message: '密码长度不能小于6个字符', trigger: 'blur' }
  ],
  confirmPassword: [
    { required: true, message: '请再次输入新密码', trigger: 'blur' },
    { min: 6, message: '密码长度不能小于6个字符', trigger: 'blur' },
    { 
      validator: (rule, value, callback) => {
        if (value !== passwordForm.newPassword) {
          callback(new Error('两次输入的密码不一致'))
        } else {
          callback()
        }
      }, 
      trigger: 'blur' 
    }
  ]
})

const showUserMenu = ref(false)

// 获取系统名称
const fetchSystemName = async () => {
  try {
    const response = await systemApi.getSystemInfo()
    systemName.value = response.systemName
  } catch (error) {
    console.error('获取系统名称失败', error)
  }
}

// 检查设备类型
const checkDeviceType = () => {
  const width = window.innerWidth
  isMobile.value = width < 768 
}

// 处理导航菜单命令
const handleNavCommand = (command: string) => {
  router.push(command)
}

// 处理用户下拉菜单命令
const handleCommand = (command: string) => {
  if (command === 'logout') {
    userApi.logout()
    router.push('/login')
  } else if (command === 'changePassword') {
    // 重置表单
    passwordForm.oldPassword = ''
    passwordForm.newPassword = ''
    passwordForm.confirmPassword = ''
    changePasswordDialogVisible.value = true
  }
}

// 提交修改密码
const submitChangePassword = async () => {
  if (!passwordFormRef.value) return
  
  await passwordFormRef.value.validate(async (valid) => {
    if (valid) {
      changePasswordLoading.value = true
        try {
          await userApi.changePassword(passwordForm.oldPassword, passwordForm.newPassword)
          ElMessage.success('密码修改成功')
          changePasswordDialogVisible.value = false
        } catch (error: any) {
          console.error('修改密码失败', error)
          ElMessage.error(error.response?.data?.message || '密码修改失败，请检查旧密码是否正确')
        } finally {
          changePasswordLoading.value = false
        }
    }
  })
}

onMounted(() => {
  // 检查用户是否为管理员
  const userGroupId = userStore.userGroupId
  if (userGroupId === '管理员') {
    isAdmin.value = true
  }
  fetchSystemName()
  checkDeviceType()
  window.addEventListener('resize', checkDeviceType)
})

onUnmounted(() => {
  window.removeEventListener('resize', checkDeviceType)
})
</script>

