<template>
  <div class="user-proxies-container">
   

    <!-- 添加流量统计卡片 -->
    <el-card class="traffic-stats-card" v-loading="trafficLoading">
      <template #header>
        <div class="card-header">
          <span>7日流量统计</span> 
          <el-button size="small" type="primary" @click="fetchMyTrafficStatus">刷新</el-button>
        </div>
      </template>
      <div class="traffic-stats">
        <div class="traffic-item">
          <div class="traffic-label">上传流量</div>
          <div class="traffic-value">{{ formatBytes(myTraffic.upload) }}</div>
        </div>
        <div class="traffic-item">
          <div class="traffic-label">下载流量</div>
          <div class="traffic-value">{{ formatBytes(myTraffic.download) }}</div>
        </div>
        <div class="traffic-item">
          <div class="traffic-label">总流量</div>
          <div class="traffic-value">{{ formatBytes(myTraffic.total) }}</div>
        </div>
        <div class="traffic-item">
          <div class="traffic-label">当前连接数</div>
          <div class="traffic-value">{{ myTraffic.links || 0 }}</div>
        </div>
      </div>
    </el-card> 
    <el-card class="system-info-card" v-loading="systemInfoLoading">
      <template #header>
        <div class="card-header">
          <span>系统公告</span>
        </div>
      </template>
      <div class="system-description">{{ systemInfo.description }}</div>
    </el-card>
    <el-card class="user-proxies-card" v-loading="loading">
      <template #header>
        <div class="card-header">
          <span>代理列表</span> 
        </div>
      </template>
      <el-table :data="userProxies" style="width: 100%; " v-loading="loading">
      <el-table-column prop="id" label="代理名称" width="180" />
      <el-table-column prop="scheme" label="代理类型" width="120"/>
      <el-table-column label="地址" show-overflow-tooltip>
        <template #default="{ row }">
           {{ getShareUrl(row) }} 
        </template>
      </el-table-column>
      <el-table-column label="操作" width="120">
        <template #default="{ row }"> 
            <el-button type="success" @click="handleGetLinkConfig(row)">完整配置</el-button> 
        </template>
      </el-table-column>
    </el-table>
    </el-card>
    <el-dialog
      v-model="linkConfigDialogVisible"
      title="连接配置"
      width="500px"
    >
      <div v-if="linkConfig">
        <el-input
          readonly
          :rows="10"
          type="textarea"
          v-model="linkConfig"
        > 
        </el-input>
        <el-button @click="copyLinkConfig" type="primary" style="width: 100%;">复制</el-button>
        <div class="qrcode-container" v-if="linkConfig">
          <el-image :src="getQrCodeUrl(linkConfig)" fit="contain" style="width: 200px; height: 200px;" />
        </div>
      </div>
      <div v-else>
        <el-empty description="获取链接失败" />
      </div>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, onUnmounted } from 'vue'
import { ElMessage } from 'element-plus'
import { proxyApi, dashboardApi, systemApi } from '../api/api'
import type { UsableInbound, MyTrafficStatus, SystemInfo } from '../api/apiTypes'

// 先声明所有响应式变量
const userProxies = ref<UsableInbound[]>([])
const loading = ref(true)
const linkConfig = ref('')
const linkConfigDialogVisible = ref(false)
const myTraffic = ref<MyTrafficStatus>({
  upload: 0,
  download: 0,
  total: 0,
  used: 0,
  remaining: 0,
  limit: 0,
  links: 0
})
const trafficLoading = ref(true)
const systemInfo = ref<SystemInfo>({
  systemName: '',
  description: '',
  version: '',
  startUpTime: '',
  trafficRecordDays: 30
})
const systemInfoLoading = ref(true)
let trafficRefreshInterval: number | null = null

// 获取系统信息
const fetchSystemInfo = async () => {
  systemInfoLoading.value = true
  try {
    systemInfo.value = await systemApi.getSystemInfo()
  } catch (error) {
    console.error('获取系统信息失败', error)
    ElMessage.error('获取系统信息失败')
  } finally {
    systemInfoLoading.value = false
  }
}

// 获取用户可用代理
const fetchUserProxies = async () => {
  loading.value = true
  try {
    userProxies.value = await proxyApi.getUsableInboundProxies()
  } catch (error) {
    console.error('获取用户代理失败', error)
    ElMessage.error('获取用户代理失败')
  } finally {
    loading.value = false
  }
}

// 获取用户流量状态
const fetchMyTrafficStatus = async () => { 
  try {
    myTraffic.value = await dashboardApi.getMyTrafficStatus()
  } catch (error) {
    console.error('获取流量状态失败', error)
    ElMessage.error('获取流量状态失败')
  } finally {
    trafficLoading.value = false
  }
}

const getShareUrl = (row: UsableInbound) => {
  let config = JSON.parse(row.linkConfig)
  while (config.upper != undefined) {
    config = config.upper
  }
  return config.url
}

// 获取二维码URL
const getQrCodeUrl = (text: string) => {
  return `https://api.qrserver.com/v1/create-qr-code/?size=200x200&data=${encodeURIComponent(text)}`
}

const handleGetLinkConfig = async (proxy: UsableInbound) => {
  try {  
    linkConfig.value = proxy.linkConfig || '' 
    linkConfigDialogVisible.value = true
  } catch (error) {
    ElMessage.error('获取连接配置失败')
    console.error(error)
  }
}

const copyLinkConfig = () => {
  navigator.clipboard.writeText(linkConfig.value)
  ElMessage.success('连接配置已复制到剪贴板')
}

// 格式化字节数
const formatBytes = (bytes: number) => {
  if (bytes === 0) return '0 B'
  
  const k = 1024
  const sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB']
  const i = Math.floor(Math.log(bytes) / Math.log(k))
  
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
}

onMounted(async () => {
  await Promise.all([
    fetchUserProxies(),
    fetchMyTrafficStatus(),
    fetchSystemInfo()
  ])
  
  // 设置定时刷新流量状态
  trafficRefreshInterval = window.setInterval(() => {
    fetchMyTrafficStatus()
  }, 5000) // 每5秒刷新一次，连接数需要更频繁更新
})

onUnmounted(() => {
  // 清除定时器
  if (trafficRefreshInterval) {
    clearInterval(trafficRefreshInterval)
  }
})
</script>

