import { createRouter, createWebHistory } from 'vue-router'
import type { RouteRecordRaw } from 'vue-router'
import MainLayout from '../layouts/MainLayout.vue'
import Login from '../views/Login.vue'
import Dashboard from '../views/Dashboard.vue' 
import System from '../views/System.vue'
import UserProxies from '../views/UserProxies.vue'
import { useUserStore } from '../stores/user' 

const routes: RouteRecordRaw[] = [
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: { requiresAuth: false }
  },
  {
    path: '/',
    component: MainLayout,
    meta: { requiresAuth: true },
    children: [
      {
        path: '',
        name: 'Dashboard',
        component: Dashboard,
        meta: { adminOnly: true }
      },
      {
        path: 'system',
        name: 'System',
        component: System,
        meta: { adminOnly: true }
      },
      {
        path: 'my-proxies',
        name: 'UserProxies',
        component: UserProxies,
        meta: { adminOnly: false }
      },
      {
        path: '/integrated',
        name: 'IntegratedManagement',
        component: () => import('../views/IntegratedManagement.vue'),
        meta: {
          title: '综合管理',
          requiresAuth: true
        }
      }
    ]
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

// 路由守卫
router.beforeEach(async (to, from, next) => {
  const userGroupId = localStorage.getItem('userGroupId')
  console.log('userGroupId', userGroupId)
  // 检查是否需要登录
  if (to.meta.requiresAuth && !userGroupId) {
    next('/login')
    return
  }
  
  // 如果已登录且访问的是根路径，根据用户角色重定向
  if (to.path === '/' && userGroupId) {
    const userStore = useUserStore()
    const userGroupId = userStore.userGroupId
    console.log('userGroupIda', userGroupId)
    if (userGroupId) {
      try { 
        // 检查是否为管理员用户组（这里假设ID为1的用户组是管理员组）
        if (userGroupId == "管理员") {
          // 管理员用户，允许访问仪表板
          next()
          return
        } else {
          // 非管理员用户，重定向到可用代理页面
          next('/my-proxies')
          return
        }
      } catch (error) {
        console.error('获取用户组信息失败', error)
      }
    }
  }
  
  // 检查管理员权限
  if (to.meta.adminOnly) {
    const userStore = useUserStore()
    const userGroupId = userStore.userGroupId
    
    if (userGroupId) {
      try { 
        // 检查是否为管理员用户组
        if (userGroupId === '管理员') {
          next()
          return
        } else {
          // 非管理员重定向到可用代理页面
          next('/my-proxies')
          return
        }
      } catch (error) {
        console.error('获取用户组信息失败', error)
        next('/login')
        return
      }
    } else {
      next('/login')
      return
    }
  }
  
  next()
})

export default router