<template>
  <div class="dashboard-container">
    <el-row :gutter="20" class="mb-4">
      <!-- 流量趋势卡片 - 在移动端占满宽度 -->
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12" class="mb-4">
        <el-card shadow="hover" v-loading="loading.trafficHistory" class="traffic-trend">
          <template #header>
            <div class="card-header">
              <span>流量趋势</span>
              <el-radio-group v-model="trafficTimeRange" size="small">
                <el-radio-button label="hour">小时</el-radio-button>
                <el-radio-button label="day">天</el-radio-button>
                <el-radio-button label="week">周</el-radio-button>
              </el-radio-group>
            </div>
          </template>
          <div class="chart-container">
            <v-chart class="chart" :option="trafficChartOption" autoresize />
          </div>
        </el-card>
      </el-col>
      
      <!-- 今日流量卡片 - 在移动端占半宽 -->
      <el-col :xs="12" :sm="12" :md="6" :lg="6" :xl="6" class="mb-4">
        <el-card shadow="hover" class="daily-traffic">
          <template #header>
            <div class="card-header">
              <span>今日流量</span>
            </div>
          </template>
          <div class="status-content">
            <div class="status-item">
              <span class="label">上传</span>
              <span class="value">{{ formatBytes(trafficStatus.upload) }}</span>
            </div>
            <div class="status-item">
              <span class="label">下载</span>
              <span class="value">{{ formatBytes(trafficStatus.download) }}</span>
            </div>
            <div class="status-item">
              <span class="label">总流量</span>
              <span class="value">{{ formatBytes(trafficStatus.total) }}</span>
            </div>
          </div>
        </el-card>
      </el-col>
      
      <!-- 在线用户卡片 - 在移动端占半宽 -->
      <el-col :xs="12" :sm="12" :md="6" :lg="6" :xl="6" class="mb-4">
        <el-card shadow="hover" class="online-users">
          <template #header>
            <div class="card-header">
              <span>在线用户</span>
            </div>
          </template>
          <el-table :data="activeUserLinks" style="width: 100%">
            <el-table-column prop="userId" label="用户ID" />
            <el-table-column prop="links" label="连接数"/> 
          </el-table>
        </el-card>
      </el-col>
    </el-row> 
    
    <el-row :gutter="20" class="mb-4">
      <!-- 出站代理流量排行 - 在移动端占半宽 -->
      <el-col :xs="12" :sm="12" :md="6" :lg="6" :xl="6" class="mb-4">
        <el-card shadow="hover" class="inbound-rank">
          <template #header>
            <div class="card-header">
              <span>入站代理流量排行 (7日)</span>
            </div>
          </template>
          <el-table :data="inboundTrafficRank" style="width: 100%">
            <el-table-column prop="name" label="代理名称" />
            <el-table-column prop="traffic" label="流量总计">
              <template #default="{ row }">
                {{ formatBytes(row.traffic) }}
              </template>
            </el-table-column> 
          </el-table>
        </el-card>
      </el-col>
      
      <!-- 入站代理流量排行 - 在移动端占半宽 -->
      <el-col :xs="12" :sm="12" :md="6" :lg="6" :xl="6" class="mb-4">
        <el-card shadow="hover" class="outbound-rank">
          <template #header>
            <div class="card-header">
              <span>出站代理流量排行 (7日)</span>
            </div>
          </template>
          <el-table :data="outboundTrafficRank" style="width: 100%">
            <el-table-column prop="name" label="代理名称" />
            <el-table-column prop="traffic" label="流量总计">
              <template #default="{ row }">
                {{ formatBytes(row.traffic) }}
              </template>
            </el-table-column> 
          </el-table>
        </el-card>
      </el-col>
      
      <!-- 用户流量排行 - 在移动端占满宽度 -->
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12" class="mb-4">
        <el-card shadow="hover" class="user-rank">
          <template #header>
            <div class="card-header">
              <span>用户流量排行 (7日)</span>
            </div>
          </template>
          <el-table :data="userTrafficRank" style="width: 100%">
            <el-table-column prop="name" label="用户名" />
            <el-table-column prop="upload" label="上传">
              <template #default="{ row }">
                {{ formatBytes(row.upload) }}
              </template>
            </el-table-column>
            <el-table-column prop="download" label="下载">
              <template #default="{ row }">
                {{ formatBytes(row.download) }}
              </template>
            </el-table-column>
            <el-table-column prop="traffic" label="流量总计">
              <template #default="{ row }">
                {{ formatBytes(row.traffic) }}
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, reactive, computed, watch, onUnmounted } from 'vue'
import { ElMessage } from 'element-plus'
import { dashboardApi } from '../api/api'
import type { SystemStatus, ProxyStatus, TrafficStatus, TrafficRecord, SystemLog } from '../api/apiTypes'
import { use } from 'echarts/core'
import { CanvasRenderer } from 'echarts/renderers'
import { LineChart } from 'echarts/charts'
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent
} from 'echarts/components'
import VChart from 'vue-echarts'

// 注册 ECharts 组件
use([
  CanvasRenderer,
  LineChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent
])
 

// 流量状态数据
const trafficStatus = ref({
  upload: 0,
  download: 0,
  total: 0
})
 

// 代理流量排行
const inboundTrafficRank = ref<TrafficRecord[]>([])
const outboundTrafficRank = ref<TrafficRecord[]>([])

// 用户流量排行
const userTrafficRank = ref([])

// 在线用户
const activeUserLinks = ref<ActiveUserLink[]>([])

// 流量图表时间范围
const trafficTimeRange = ref('day')

// 流量历史数据
const trafficHistory = ref({
  labels: [],
  upload: [],
  download: []
})
 

// 加载状态
const loading = reactive({
  systemStatus: true,
  proxyStatus: true,
  trafficStatus: true, 
  inboundTrafficRank: true,
  outboundTrafficRank: true,
  userTrafficRank: true,
  trafficHistory: true,
  recentLogs: true,
  activeUserLinks: true
})

 

// 获取流量状态
const fetchTrafficStatus = async () => {
  loading.trafficStatus = true
  try {
    trafficStatus.value = await dashboardApi.getTrafficStatus()
  } catch (error) {
    console.error('获取流量状态失败', error)
    ElMessage.error('获取流量状态失败')
  } finally {
    loading.trafficStatus = false
  }
}

// 获取代理流量排行
const fetchProxyTrafficRank = async () => {
  loading.inboundTrafficRank = true
  loading.outboundTrafficRank = true
  dashboardApi.getProxyTrafficRank('i').then((res) => {
    inboundTrafficRank.value = res
  }).catch((error) => {
    console.error('获取入站代理流量排行失败', error)
    ElMessage.error('获取入站代理流量排行失败')
  }).finally(() => {
    loading.inboundTrafficRank = false
  })
  dashboardApi.getProxyTrafficRank('o').then((res) => {
    outboundTrafficRank.value = res
  }).catch((error) => {
    console.error('获取出站代理流量排行失败', error)
    ElMessage.error('获取出站代理流量排行失败')
  }).finally(() => {
    loading.outboundTrafficRank = false
  })
}

// 获取用户流量排行
const fetchUserTrafficRank = async () => {
  loading.userTrafficRank = true
  try {
    userTrafficRank.value = await dashboardApi.getUserTrafficRank()
  } catch (error) {
    console.error('获取用户流量排行失败', error)
    ElMessage.error('获取用户流量排行失败')
  } finally {
    loading.userTrafficRank = false
  }
}
const fetchActiveUserLinks = async () => {
  loading.activeUserLinks = true
  try {
    activeUserLinks.value = await dashboardApi.getActiveUserLinks()
  } catch (error) {
    console.error('获取在线用户失败', error)
    ElMessage.error('获取在线用户失败')
  } finally {
    loading.activeUserLinks = false
  }
}

// 获取流量历史数据
const fetchTrafficHistory = async () => {
  loading.trafficHistory = true
  try {
    trafficHistory.value = await dashboardApi.getTrafficHistory(trafficTimeRange.value)
  } catch (error) {
    console.error('获取流量历史数据失败', error)
    ElMessage.error('获取流量历史数据失败')
  } finally {
    loading.trafficHistory = false
  }
}
 

// 流量图表配置
const trafficChartOption = computed(() => {
  return {
    tooltip: {
      trigger: 'axis',
      formatter: function(params: any) {
        let result = params[0].name + '<br/>';
        params.forEach((item: any) => {
          result += item.seriesName + ': ' + formatBytes(item.value) + '<br/>';
        });
        return result;
      }
    },
    legend: {
      data: ['上传流量', '下载流量']
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '3%',
      containLabel: true
    },
    xAxis: {
      type: 'category',
      boundaryGap: false,
      data: trafficHistory.value.labels || []
    },
    yAxis: {
      type: 'value',
      axisLabel: {
        formatter: function(value: number) {
          return formatBytes(value);
        }
      }
    },
    series: [
      {
        name: '上传流量',
        type: 'line', 
        data: trafficHistory.value.upload || [],
        areaStyle: {
          opacity: 0.3
        },
        lineStyle: {
          width: 2
        },
        itemStyle: {
          color: '#409EFF'
        }
      },
      {
        name: '下载流量',
        type: 'line', 
        data: trafficHistory.value.download || [],
        areaStyle: {
          opacity: 0.3
        },
        lineStyle: {
          width: 2
        },
        itemStyle: {
          color: '#67C23A'
        }
      }
    ]
  };
});

// 监听流量时间范围变化
watch(trafficTimeRange, () => {
  fetchTrafficHistory()
})

// 监听流量历史数据变化，更新图表
watch(trafficHistory, () => {
  // 图表会自动更新，因为 trafficChartOption 是计算属性
}, { deep: true })

// 格式化字节数
const formatBytes = (bytes: number) => {
  if (bytes === 0) return '0 B'
  
  const k = 1024
  const sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB']
  const i = Math.floor(Math.log(bytes) / Math.log(k))
  
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
}



// 初始化数据
onMounted(async () => {
  await Promise.all([
    fetchTrafficStatus(), 
    fetchProxyTrafficRank(),
    fetchUserTrafficRank(),
    fetchTrafficHistory(),   
    fetchActiveUserLinks()
  ])
}) 
const refresher = setInterval(async () => {
  fetchTrafficStatus()
  fetchActiveUserLinks()
}, 5000)

onUnmounted(() => {
  clearInterval(refresher)
})

</script>

