// API基础配置
const API_BASE_URL = import.meta.env.VITE_API_BASE_URL || '/api';
//const API_BASE_URL = 'https://zixtdeair.local:8083/api';
// 创建axios实例
const apiClient = axios.create({
  baseURL: API_BASE_URL,
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
  }
});

// 请求拦截器：添加 token
apiClient.interceptors.request.use(
  (config) => {
    // 从 localStorage 获取 token
    const token = localStorage.getItem('token');
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// 响应拦截器：处理响应和错误
apiClient.interceptors.response.use(
  (response) => {
    // 直接返回data部分
    return response.data;
  },
  (error) => {
    // 处理错误
    if (error.response) {
      // 服务器返回错误状态码
      if (error.response.status === 401) {
        if (window.location.pathname !== '/login') {
          // 未授权，清除 token 并重定向到登录页面
          localStorage.removeItem('token');
          window.location.href = '/login';
        }
      }
      return Promise.reject(error.response.data);
    } else if (error.request) {
      // 请求发出，但没有收到响应
      return Promise.reject({ code: -1, message: '网络错误，请稍后重试' });
    } else {
      // 请求配置出错
      return Promise.reject({ code: -2, message: '请求错误' });
    }
  }
);

// 通用API请求函数
const request = {
  get: async <T>(url: string, params?: any): Promise<T> => {
    const response = await apiClient.get<ApiResponse<T>>(url, { params });
    return (response as unknown as ApiResponse<T>).data as T;
  },
  
  post: async <T>(url: string, data?: any): Promise<T> => {
    const response = await apiClient.post<ApiResponse<T>>(url, data);
    return (response as unknown as ApiResponse<T>).data as T;
  },
  
  put: async <T>(url: string, data?: any): Promise<T> => {
    const response = await apiClient.put<ApiResponse<T>>(url, data);
    return (response as unknown as ApiResponse<T>).data as T;
  },
  
  delete: async <T>(url: string): Promise<T> => {
    const response = await apiClient.delete<ApiResponse<T>>(url);
    return (response as unknown as ApiResponse<T>).data as T;
  }
};

