async function sha256(message: string) {
  // 将文本编码成 Uint8Array
  const encoder = new TextEncoder();
  const data = encoder.encode(message);
  
  // 计算SHA-256
  const hashBuffer = await crypto.subtle.digest('SHA-256', data);
  
  // 将返回的 ArrayBuffer 转换为十六进制字符串
  const hashArray = Array.from(new Uint8Array(hashBuffer));
  const hashHex = hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
  
  return hashHex;
}

// 用户API
export const userApi = {
  getUsers: () => request.get<User[]>('/users'),
  getUser: (id: string) => request.get<User>(`/users/${id}`),
  createUser: async (userData: CreateUserRequest) => {
    userData.password = (await sha256(userData.password)).toString()
    return request.post<User>('/users', userData)
  },
  updateUser: (id: string, userData: UpdateUserRequest) => request.put<User>(`/users/${id}`, userData),
  deleteUser: (id: string) => request.delete<boolean>(`/users/${id}`), 
  resetPassword: (id:string) => request.post<boolean>(`/users/${id}/reset-password`),
  
  // 修改登录方法
  login: async (credentials: LoginRequest) => {
    credentials.password = (await sha256(credentials.password)).toString()
    return request.post<LoginResponse>('/auth/login', credentials)
  },
  
  // 修改登出方法
  logout: () => {
    // 客户端只需清除 token，不需要服务器端操作
    const userStore = useUserStore()
    userStore.logout()
    return true
  },
  
  // 修改密码
  changePassword: async (oldPassword: string, newPassword: string) => {
    const oldPasswordHash = (await sha256(oldPassword)).toString()
    const newPasswordHash = (await sha256(newPassword)).toString()
    return request.post<boolean>('/auth/change-password', {
      oldPassword: oldPasswordHash,
      newPassword: newPasswordHash
    })
  }
};

